import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import HomePage from '../components/HomePage'
import AboutusPage from '../components/AboutusPage'
export default new VueRouter({
    routes: [
        {
            path: '/', 
            component: HomePage,
        },
        {
            path: '/aboutus', 
            component: AboutusPage,
        },
    ],
})